const resetWholeGeneration = () => {
    window.location.reload();
}
const binNumArr = [128, 64, 32, 16, 8, 4, 2, 1];
const cloneBinArr = Array.from(binNumArr);
const popNumArr = [];
let decimVal = 0;
let noLeadBinary;
let clickedObjectContainer;
let givenAns = document.getElementById('combAns');
let addUpresult = 0;
let corAnsArray = [];
/****************************************************************** */
decimVal = Math.floor(Math.random() * 256);
let deimalVlaue = document.getElementById("decimal-Value");
deimalVlaue.innerHTML = decimVal;
/********************************************************************************** */
function populateAnsComb() {
    let agregate = '';
    for (let k = 0; k < popNumArr.length; k++) {
        if (agregate == '') {
            agregate = agregate.concat(popNumArr[k]);
            givenAns.innerHTML = agregate;
        } else {
            agregate = agregate.concat('+', popNumArr[k]);
            givenAns.innerHTML = agregate;
        }

    }
}

function givenAnsAddUp() {
    let takeNum = clickedObjectContainer.textContent;
    takeNum = parseInt(takeNum);
    popNumArr.push(takeNum);
    console.log(popNumArr);
    // console.log(takeNum);
    addUpresult = addUpresult + takeNum;
    console.log(addUpresult);
    populateAnsComb();

} //~~~~~~~~~End of Function givenAnsAddUp
/******************************************************************************************************************************************** */

//~~~~~~~~~~~~~~~~~asked question loop~~~~~~~~~~~~~~~~
/*//////////FUNCTIONS/////////*/

// console.log(cloneBinArr);




/*********************************************End of random shuffling of the options**************/

function runToCheck() {
    // let hidTable = document.querySelector('.answersTable').style.display = 'none';
    //~~~~~~~~~~~~~~~~function definition for correct
    let resultAns = document.getElementById('matchField');
    function myFunctionCorrect() {
        resultAns.innerHTML = 'Correct!';
        document.body.style.backgroundColor = '#96E29A'; //color feedback correct
        givenAns.style.color = '#22E02D';
    } //end of function myFunctionCorrect
    //~~~~~~~~~~~~~~~~function definition for False
    function myFunctionFalse() {
        //injecting a class for changing the color of the FALSE text to red
        resultAns.innerHTML = 'False!';
        document.body.style.backgroundColor = '#FF5760'; //color feedback correct
        resultAns.style.color = '#FF0000';
        function findTheCorrectAns() {
            for (let m = 0; m < 8; m++) {

                if ((decimVal - cloneBinArr[m]) < 0) {
                    // console.log(`iterration ${m} ,decimal is smaller than : ${cloneBinArr[m]}`);
                    continue;
                } else if (decimVal == cloneBinArr[m]) {
                    // console.log(`${cloneBinArr[m]} is the answer`);
                }
                decimVal = decimVal - cloneBinArr[m];
                corAnsArray.push(cloneBinArr[m]);
                // console.log(`on iterration ${m},subtracted value is: ${decimVal} and the number is: ${cloneBinArr[m]}`);
            }
            console.log(corAnsArray);
        }
        findTheCorrectAns();
        if (addUpresult == '') {
            givenAns.innerHTML = `Correct answer is: ${corAnsArray}, and you didn't choose an option`
        } else {
            givenAns.innerHTML = `Correct answer is: ${corAnsArray}. You Chose ${popNumArr} which yields: ${addUpresult}!`;
        }
    }

    //~~~~~~~~~~~~~~~~Main condition~~~~~~~~~~~~~////////////////////////////////
    let cmbAnsFieldContent = givenAns.innerText;
    if (clickedObjectContainer != undefined) {
        if (decimVal == addUpresult) {
            myFunctionCorrect();
        } else {
            myFunctionFalse();
        }

    } else {
        myFunctionFalse();
    }
} //end of function runToCheck

